/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

/**
 * Basic styling
 */
html {
	line-height: 1;
    background-color: $background-color;

	body {
		line-height: 1.5em;
		font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
		color: $text-color;
		-webkit-text-size-adjust: 100%;
		-webkit-font-feature-settings: "kern" 1;
		-moz-font-feature-settings: "kern" 1;
		-o-font-feature-settings: "kern" 1;
		font-feature-settings: "kern" 1;
		font-kerning: normal;
		max-width: 1200px;
		position: relative;
		margin: 0 auto;
		background: #f8f8f8 url("/img/noise.png") repeat scroll left top;

		> header {
			background: #333 url("/img/cabecera.jpg") no-repeat scroll left top;
			font-size: 1em;
			height: 181px;
			padding-bottom: 1.5em;
			padding-top: 1.5em;
			padding-left: 18px;
			padding-right: 18px;

			h1 {
				font-weight: normal;
				margin-bottom: 0.6667em;
				display: inline-block;
				margin: 0;

				a, a:visited, a:hover {
					color: #f2f2f2;
					text-decoration: none;
				}
			}

			h2 {
				margin: .2em 0 0;
				font-size: 1em;
				color: #aaa;
				font-weight: normal;
			}
		}

		/**
		 * Set `margin-bottom` to maintain vertical rhythm
		 */
		h1, h2, h3, h4, h5, h6,
		p, blockquote, pre,
		ul, ol, dl, figure,
		%vertical-rhythm {
			margin-bottom: $spacing-unit / 2;
		}

		/**
		 * Images
		 */
		img {
			max-width: 100%;
			vertical-align: middle;
		}

		/**
		 * Figures
		 */
		figure > img {
			display: block;
		}

		figcaption {
			font-size: $small-font-size;
		}

		/**
		 * Lists
		 */
		ul, ol {
			margin-left: $spacing-unit;
		}

		li {
			> ul,
			> ol {
				margin-bottom: 0;
			}
		}

		/**
		 * Headings
		 */
		h1, h2, h3, h4, h5, h6 {
			font-weight: $base-font-weight;
		}

		/**
		 * Links
		 */
		a {
			color: #468;
			text-decoration: none;

			&:visited {
				color: darken($brand-color, 15%);
			}

			&:hover {
				color: $text-color;
				text-decoration: underline;
			}
		}

		/**
		 * Blockquotes
		 */
		blockquote {
			color: $grey-color;
			border-left: 4px solid $grey-color-light;
			padding-left: $spacing-unit / 2;
			font-size: 18px;
			letter-spacing: -1px;
			font-style: italic;

			> :last-child {
				margin-bottom: 0;
			}
		}

		/**
		 * Code formatting
		 */
		pre,
		code {
			font-size: 15px;
			border: 1px solid $grey-color-light;
			border-radius: 3px;
			background-color: #eef;
		}

		code {
			padding: 1px 5px;
		}

		pre {
			padding: 8px 12px;
			overflow-x: auto;

			> code {
				border: 0;
				padding-right: 0;
				padding-left: 0;
			}
		}

		/**
		 * Wrapper
		 */
		.wrapper {
			max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
			max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));
			margin-right: auto;
			margin-left: auto;
			padding-right: $spacing-unit;
			padding-left: $spacing-unit;
			@extend %clearfix;

			@include media-query($on-laptop) {
				max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
				max-width: calc(#{$content-width} - (#{$spacing-unit}));
				padding-right: $spacing-unit / 2;
				padding-left: $spacing-unit / 2;
			}
		}

		/**
		 * Clearfix
		 */
		%clearfix {
			&:after {
				content: "";
				display: table;
				clear: both;
			}
		}

		/**
		 * Icons
		 */
		.icon {
			> svg {
				display: inline-block;
				width: 16px;
				height: 16px;
				vertical-align: middle;

				path {
					fill: $grey-color;
				}
			}
		}

		.pagination {
			text-align: center;
			padding-left: 3em;

			em {
				font-style: normal;
			}
		}

		.rss-subscribe {
			float: right;
			padding-right: 3em;
		}
	}
}
